export default {
  data () {
    return {
      sizeTitle: ''
    }
  },
  props: {
    size: {
      type: String,
      default: ''
    },
    isSelected: {
      type: Boolean,
      default: false
    }
  },
  created () {
    if (this.size === 'small') {
      this.sizeTitle = '小'
    } else if (this.size === 'medium') {
      this.sizeTitle = '中'
    } else {
      this.sizeTitle = '大'
    }
  },
  methods: {
    selectSize () {
      this.$store.commit('data/changeFontsize', this.size)
    }
  }
}
